import './App.css';
import {v4 as UID} from 'uuid';
import { useState, useEffect } from 'react';

function App() {
  let [isLoaded, setIsLoaded] = useState(false);
  const [name, setName] = useState("");
  const [newOperation, setNewOperation] = useState({name: "", cost: "", md: "", d: ""});
  const [operations, setOperations] = useState([{ id: UID(), name: "Name of operation", cost: "100", md: "011", d: "210" }]);

  useEffect(() => {
    if (isLoaded)
      localStorage.setItem("operations", JSON.stringify(operations));
  }, [operations])  

  useEffect(() => {
    if (isLoaded)
      localStorage.setItem("name", name);
  }, [name])

  useEffect(() => {
    if (localStorage.getItem("operations") != null)
      setOperations(JSON.parse(localStorage.getItem("operations")));
    if (localStorage.getItem("name") != null) 
      setName(localStorage.getItem("name"));
    setIsLoaded(true);
  }, [])

  let operationindex = 1;

  async function createOne() {
    let n = name;
    while (n.length < 5) {
      console.log(name);
      console.log(name.length < 5);
      n = prompt("Musite vyplnit nazev, ktery je alespon 5 znaku :D");
      setName(n);
    }
    // eslint-disable-next-line no-restricted-globals
    const yes = confirm("Jste si jisty ze chcete tuto sablonu pouzit?");
    if (!yes) return;
    const token = prompt("Napiste svuj kod pro autorizaci... ");
    const auth = await fetch("/create", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        operations: operations,
        token: token,
        name: name
      })
    });

    if (auth.status == 404) 
      alert("Tato aplikace nemuze bezet bez serveru, ktery ma za ucelem pripojeni k databazi,.. prosim pripojte se k serveru... nebo zkontrolujte pripojeni k internetu.");
    else if (auth.status == 403) 
      alert("Pristup odmitnut vas kod (token) byl odmitnut serverem protoze v nem neni ulozen.");
    else if (auth.status == 200) {
      setTimeout(()=> {
        alert("Vas vytvor byl ulozen do databaze a budete presmerovani na hlavni stranku");
        setOperations([]);
        setName("");
        window.location.href="/";
      }, 1000);
    }
    else alert("Neco se pokazilo pri pokusu o navazani pripojeni k serveru...");
  }

  function isItTrueNumber(value, min) {
    let setto = value[Object.keys(value)[0]];
    if (setto == "") setto = "0";

    if ( !( String(setto).match(/^[0-9]+$/) && setto.length >= min)) return false;
    setNewOperation({...newOperation, ...value});
    return;
  }

  function isItBetween(value, min, max) {
    let setto = value[Object.keys(value)[0]].length;
    return setto <= max && setto >= min ? isItTrueNumber(value, min): false;
  }

  function add() {
    for (const properties of Object.values(newOperation))
      if (properties.length < 1) return;
    
    setOperations([
      ...operations, {
        ...newOperation, id: UID()
      }
    ])
    setNewOperation({name: "", cost: "", md: "", d: ""})
  }

  const del = (id) => setOperations([...operations.filter(o => o.id != id)])

  return (
    <div className="App container mt-5">
      <table className="table table-hover table-dark ">
        <thead>
          <tr>
            <th></th>
            <th colSpan="4" className="" id="addon">
              <div className="input-group my-5">
                <label htmlFor="name" className="input-group-text bg-secondary" aria-describedby="addon">Nazev: </label>
                <input type="text" placeholder="Zadejte nazev" className="form-control bg-dark text-white text-center fs-4" value={name} onChange={e => setName(e.target.value)}  />
              </div>
            </th>
            <th></th>
          </tr>
          <tr>
            <th>Operace</th>
            <th>Jmeno</th>
            <th>Castka</th>
            <th>MD</th>
            <th>D</th>
            <th>{/* delete buttons */}</th>
          </tr>
        </thead>
        <tbody>
          {
            operations.map((operation, i) => {
              operationindex++;
              return <tr>
                <td>{i +1}</td>
                <td>{operation.name}</td>
                <td>{operation.cost}</td>
                <td>{operation.md}</td>
                <td>{operation.d}</td>
                <td className="justify-content-between d-flex" >
                  <button  value={operation.id} onClick={e => del(e.target.value)} className="form-control btn btn-danger">X</button>
                </td>
              </tr>
            })
          }
        </tbody>
        <tfoot>
          <tr>
            <td>
              {operationindex}
            </td>
            <td className="col-7">
              <input className="form-control bg-dark text-white text-center" type="text" 
                value={newOperation.name} onChange={e => setNewOperation({...newOperation, name: e.target.value})} placeholder="Napiste nazev operace" />
            </td>
            <td className="col-2">
              <input className="form-control bg-dark text-white text-center" type="text"
                 value={newOperation.cost} onChange={e => isItTrueNumber({cost: e.target.value}, 0)} placeholder="Castka..." />
            </td>
            <td className="col-1">
              <input className="form-control bg-dark text-white text-center" type="text" 
                value={newOperation.md} onChange={e => isItBetween({md: e.target.value}, 0, 3)} placeholder="Ma dati..." />
            </td>
            <td className="col-1">
              <input className="form-control bg-dark text-white text-center" type="text" 
                value={newOperation.d} onChange={e => isItBetween({d: e.target.value}, 0, 3)} placeholder="Dal..." />
            </td>
            <td>
              <button onClick={add} className="btn btn-success form-control">+</button>
            </td>
          </tr>
        </tfoot>
      </table>
      <div className="flaoting">
        <button onClick={e => 
          // eslint-disable-next-line no-restricted-globals
          confirm("Opravdu? Tato akci snaze vsechny operace a nazev...") ? setOperations([]) && setName("") : ""
          } className="fs-4 btn me-3 btn-danger">Smazat</button>
        <button onClick={createOne} className="fs-4 btn btn-primary">Vytvorit</button>
      </div>
    </div>
  );
}

export default App;
